import ReactModal from "react-modal";

import { colors } from "../styleguide";

import { ModalSizes, ModalVariants, StyledModalProps } from "./Modal.types";

export const modalWidth: Record<ModalSizes, string> = {
	small: "400px",
	medium: "512px",
	large: "752px",
	extra_large: "1080px",
	fullScreen: "100%",
};

type GetCustomModalStyles = (styleProps: StyledModalProps) => ReactModal.Styles;

const getModalWidth = (variant: ModalVariants, size?: ModalSizes) => {
	if (variant === "destructive") {
		return modalWidth.small;
	}
	if (variant === "scrollable") {
		return modalWidth.medium;
	}
	if (variant === "graphic") {
		return modalWidth.small;
	}
	if (variant === "graphic_large") {
		return modalWidth.medium;
	}
	return size && modalWidth[size];
};

export const getCustomModalStyles: GetCustomModalStyles = ({ size, variant }) => ({
	overlay: {
		backgroundColor: "transparent",
		zIndex: 9999,
	},
	content: {
		width: variant && getModalWidth(variant, size),
		backgroundColor: variant?.includes("graphic") ? colors.black : colors.white,
		padding: "0",
		display: "flex",
		flexDirection: "column",
		height: size === "fullScreen" ? "100%" : "fit-content",
		position: "fixed",
		top: "50%",
		left: "50%",
		transform: "translate(-50%, -50%)",
		// eslint-disable-next-line max-len
		boxShadow: "0px 60px 240px rgba(0, 0, 0, 0.5), 0px 32px 32px -8px rgba(0, 0, 0, 0.08), 0px 16px 16px rgba(0, 0, 0, 0.08), 0px 8px 8px rgba(0, 0, 0, 0.08), 0px 4px 4px rgba(0, 0, 0, 0.08), 0px 2px 2px rgba(0, 0, 0, 0.08)",
		borderRadius: size !== "fullScreen" ? "16px" : "0",
		border: "none",
	},
});
